.error {
  background-color: #ff460d;
  color: #fff;
  margin-left: -16px;
  margin-right: -16px;
  padding: 10px 16px;
}

.disabled {
  cursor: not-allowed;
  opacity: 0.65;
}

.disabled > * {
  pointer-events: none;
}

.highlight {
  border-radius: 2px;
  box-shadow: 0 0 0 2px var(--colors-Warn);
}
